<template>
  <div>
    <LayoutBox one="热门设置">
      <el-form label-position="right">
        <el-form-item label="热门条件">
          <div class="popular_conditions">
            <span style="width: 40px">设置</span>
            <el-input v-model.number="hotNumber" style="width: 60px" @change="onLikeNumber"></el-input>
            <span style="width: 100px">点赞可以上热门</span>
          </div>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'video_set',

  components: { LayoutBox },

  data () {
    return {
      hotNumber: '', // 点赞上热门数量
      id: '', // 点赞上热门数量的主键id
    }
  },

  methods: {
    // 修改热门条件点赞数
    onLikeNumber () {
      this.apiVideoHotUpdate()
    },

    // api---------------------
    // // 新增
    // apiVideoHotInsert () {
    //   let data = {
    //     hotNumber: 1,
    //   }
    //   this.$http.post('/woho/video/hot/insert', data)
    // },
    // 更新
    apiVideoHotUpdate () {
      let data = {
        hotNumber: this.hotNumber,
        id: this.id
      }
      this.$http.put('/woho/video/hot/update', data)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('保存成功')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 分页查询
    apiVideoHotQuery () {
      this.$http.post('/woho/video/hot/query', { pageNo: 1 })
        .then((res) => {
          this.hotNumber = res.data.data.list[0].hotNumber
          this.id = res.data.data.list[0].id
        })
    }
  },

  created () {
    this.apiVideoHotQuery()
  }
}
</script>

<style lang="scss" scoped>
  .popular_conditions{
    display: flex;

  }
</style>
